import React from 'react'
import { Dropdown } from 'react-bootstrap'
import { ruleFilters } from './utils'
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper'

export default function TriggerFilters(props){

  const { filterTabs } = props
  const options = ruleFilters(props)
  return <Dropdown.Menu className='w-100 overflow-auto px-0' style={{ maxHeight: 400 }}>
    {options.map((item,index)=>{
      return !(filterTabs && filterTabs.hasOwnProperty(item.propName)) ? <Dropdown.Item
      className="d-flex align-items-center border-bottom py-2 rounded-0"
      onClick={item.fnc} key={index}>
      <div className="symbol symbol-36 bg-light-primary me-2 rounded-1">
        <FontAwesomeIcon icon={item.icon} iconClass="text-primary fs-6 me-0" />
      </div>
      <div>
          <p className={`fs-9 fw-bold mb-0 text-gray-800`}>{item.name}</p>
          <span className="fs-9 text-gray-500">{item.desc}</span>
      </div>
      </Dropdown.Item> : null
    })}
  </Dropdown.Menu>
}

