import { Dropdown } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';

const TShowHide = ({onSelectVisibility, handleTrigger, selectedVisibility}) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={"Posts Visibility"} desc={"Choose the visibility of posts"} icon={"eye"} method={()=>handleTrigger("showHide", "selectedVisibility", [])}/>
      <Dropdown className="w-100">
        <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
          <div className="d-flex align-items-center flex-grow-1">
            {selectedVisibility && selectedVisibility.length ==1 ? <FontAwesomeIcon icon={selectedVisibility.includes("1") ? "eye" : "eye-slash"} className="text-muted" /> :null}
            <p className="fs-9 fw-medium mb-0">{selectedVisibility.length > 0 ? selectedVisibility.length==1 ? selectedVisibility.includes("1") ? "Public" : "Private" :`${selectedVisibility.length} selected` :"Select Posts Visibility"}</p>
          </div>
        </Dropdown.Toggle>
        <Dropdown.Menu className="w-100">
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="tri-show-assets">
              <FontAwesomeIcon icon={'eye'} iconClass="text-muted fs-6" />
              <p className="fs-9 fw-medium mb-0">Public</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value='' id="tri-show-assets" onChange={event => onSelectVisibility("1","filter")} checked={selectedVisibility.includes("1")} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="tri-hide-assets">
              <FontAwesomeIcon icon={'eye-slash'} iconClass="text-muted fs-6" />
              <p className="fs-9 fw-medium mb-0">Private</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value='' id="tri-hide-assets" onChange={event => onSelectVisibility("2","filter")} checked={selectedVisibility.includes("2")} />
          </div>
        </Dropdown.Menu>
      </Dropdown>
    </div>
    <div className='action_gap'></div>
  </>
export default TShowHide