import { SET_TAGS_LIST } from "../reducer/keys"
import { IS_API_ACCESS_LIVE, WASABI_PATH } from "./api"
import HttpClient from "./http-client"
export const getTags = (ownerId) => {
    return (dispatch) => {
        let tags = []
        const path = IS_API_ACCESS_LIVE ? WASABI_PATH : `${WASABI_PATH}test/tagshop/`
        new HttpClient().get(`${path}media/${ownerId}/tags_${ownerId}.json`).then(response=>{
            const { data } = response
            tags = Object.values(data).filter(value => value !== "").map(item => ({ value: item, label: item }));
            dispatch({type: SET_TAGS_LIST, payload: tags})
        }).catch(ex=>{
            dispatch({type: SET_TAGS_LIST, payload: tags})
        })
    }
}