import { generateRandomArray } from '../../../../utils'

export default function RuleListSkel() {

    return ( 
        <div>
            <div className='w-100'>
                <div className='border-bottom p-4 h-56px f-center justify-content-start'>
                    <div className="sk_line w-100px d-block h-14px"></div>
                </div>
            </div>
            <div className='d-flex flex-row align-items-center border-bottom py-3 px-4 h-46px'>
                <div className='w-25'>
                    <div className="sk_line h-14px w-90px"></div>
                </div>
                <div className='w-25'>
                    <div className="sk_line h-14px w-40px"></div>
                </div>
                <div className='w-25'>
                    <div className="sk_line h-14px w-80px"></div>
                </div>
                <div className='w-25 f-center justify-content-end'>
                    <div className="sk_line h-14px w-60px"></div>
                </div>
            </div>

            {generateRandomArray(3).map((_,index) => <div className='d-flex align-items-center flex-row border-bottom py-2 px-4 h-48px'>
                <div className='w-25'>
                    <div className='d-flex align-items-center flex-row'>
                        <div className='sk_line w-60px h-12px pe-4'></div>
                    </div>
                </div>
                <div className='w-25'>
                    <div className='sk_line w-32px h-12px me-2'></div>
                </div>
                <div className='w-25'>
                    <div className="pe-3">
                        <div className='sk_line w-90px h-12px'></div>
                    </div>
                </div>
                <div className='w-25 f-center justify-content-end'>
                    <div className='sk_rect h-20px w-20px'></div>
                </div>
            </div>)}
            
        </div>
    )
}
