import BoxDesign from '../BoxDesign';

const TDuplicate = ({ handleTrigger, duplicateAssets }) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={"Duplicate Assets"} icon={"clone"} method={()=>handleTrigger("duplicateAsset", "selectedDuplicateAsset", false)}
      desc={parseInt(duplicateAssets) > 0 ? `${parseInt(duplicateAssets)} Posts Selected` : `Select Posts`} del={true}/>
    </div>
    <div className='action_gap'></div>
  </>

export default TDuplicate