import { Dropdown } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';

const TSentiment = ({handleTrigger, selectedSentiment, onSelectSentiment, selectAllSentiments}) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={"Sentiment"} desc={"Choose sentiment analysis."} icon={"face-smile"} method={()=>handleTrigger("sentiment", "selectedSentiment", [])}/>
      <Dropdown className="w-100" autoClose="outside">
        <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
          <div className="d-flex align-items-center flex-grow-1">
            {selectedSentiment && selectedSentiment.length== 1 ? 
              <FontAwesomeIcon icon={`face-${selectedSentiment.includes("1") ? "smile" : selectedSentiment.includes("2") ? "meh" : "frown"}`} iconClass="text-muted" />
            : null}
            <p className="fs-9 fw-medium mb-0">{selectedSentiment.length > 0 ? selectedSentiment.length==1 ? selectedSentiment.includes("1") ? "Positive" : selectedSentiment.includes("2") ? "Neutral" : "Negative":`${selectedSentiment.length} selected` :"Select Sentiment Type"}</p>
          </div>
        </Dropdown.Toggle>
        <Dropdown.Menu className="w-100">
          <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
            <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="allSentiments">
              Select All
            </label>
            <input className="form-check-input" type="checkbox" name="" id="allSentiments" onChange={event=>selectAllSentiments(event)} checked={selectedSentiment.length==3} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="positive">
              <FontAwesomeIcon icon={'face-smile'} iconClass="text-muted fs-6" />
              <p className="fs-9 fw-medium mb-0">Positive</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value={'image'} id="positive" onChange={event=>onSelectSentiment("1")} checked={selectedSentiment.includes("1")} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="neutral">
                <FontAwesomeIcon icon={'face-meh'} iconClass="text-muted fs-6" />
                <p className="fs-9 fw-medium mb-0">Neutral</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value={'video'} id="neutral" onChange={event=>onSelectSentiment("2")} checked={selectedSentiment.includes("2")} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="negative">
              <FontAwesomeIcon icon={'face-frown'} iconClass="text-muted fs-6" />
              <p className="fs-9 fw-medium mb-0">Negative</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value={'text'} id="negative" onChange={event=>onSelectSentiment("3")} checked={selectedSentiment.includes("3")} />
          </div>
        </Dropdown.Menu>
      </Dropdown>
    </div>
    <div className='action_gap'></div>
  </>

export default TSentiment