export const ruleFilters = ({duplicateAssets, handleTrigger}) => {
    return [
        {name:"Network", desc:"Select your preferred network", icon:"chart-network", fnc:()=>handleTrigger("networks"), propName: "networks"},
        {name:"Feeds", desc:"Select the Feed type", icon:"image-portrait", fnc:()=>handleTrigger("feeds"), propName: "feeds"},
        {name:"Media Type", desc:"Choose the type of Media", icon:"image", fnc:()=>handleTrigger("assetsType"), propName: "assetsType"},
        {name:"AI Recommendation", desc:"Choose Ai recommendation", icon:"sparkles", fnc:()=>handleTrigger("recommend"), propName: "recommend"},
        {name:"Tag", desc:"Select the preferred tag", icon:"tags", fnc:()=>handleTrigger("tags"), propName: "tags"},
        {name:"Sentiment", desc:"Choose sentiment analysis", icon:"face-smile", fnc:()=>handleTrigger("sentiment"), propName: "sentiment"},
        {name:"Public/Private", desc:"Select Posts Visibility", icon:"users", fnc:()=>handleTrigger("showHide"), propName: "showHide"},
        {name:"By Handle", desc:"Enter Handle", icon:"at", fnc:()=>handleTrigger("handle"), propName: "handle"},
        {name:"By Hashtag", desc:"Enter Hashtag", icon:"hashtag", fnc:()=>handleTrigger("hashtag"), propName: "hashtag"},
        {name:"By Keyword", desc:"Enter Keyword", icon:"text", fnc:()=>handleTrigger("keyword"), propName: "keyword"}, 
        {name:"Profanity", desc:"Apply Profanity", icon:"ban", fnc:()=>handleTrigger("profanity"), propName: "profanity"}, 
        {name:"Duplicate Post", desc:parseInt(duplicateAssets) > 0 ? `${parseInt(duplicateAssets)} Select Posts` : `Select Posts`, icon:"clone", fnc:()=>handleTrigger("duplicateAsset"), propName: "duplicateAsset"},
    ]
}

export const filtersList = ['feeds', 'networks', 'handle', 'hashtag', 'keyword', 'showHide', 'tags', 'assetsType', 'duplicateAsset', 'sentiment', 'recommend', 'profanity']

export const ruleActnFilters = (handleAction) => {
    return [{name: "Public", icon:"eye", fnc:()=>handleAction("public"), propName: "public"},
        {name:"Private", icon:"eye-slash", fnc:()=>handleAction("private"), propName: "private"},
        {name:"Assign Tag", icon:"tag", fnc:()=>handleAction("addTag"), propName: "addTag"},
        {name:"Delete", icon:"trash-can", fnc:()=>handleAction("delete"), propName: "delete"},
    ]
}

export const planUpgrade = (navigate) => {
    Swal.fire({
        title: `Upgrade to Create Rules`,
        text: `Oops! Looks like you need to upgrade your plan in order to create or modify rules.`,
        icon: 'error',
        showCancelButton: true,
        cancelButtonText: "Cancel",
        confirmButtonText: "Upgrade Now!",

    }).then((result) => {
        if (result.value) {
            navigate('/price')
        }
        else if (result.dismiss === "cancel") { }
    });
}

