import React from 'react'
import BoxDesign from './BoxDesign'

export default function CommonActions({icon, name, handleAction, val, index}) {
  return <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
        <BoxDesign name={name} icon={icon} method={()=>handleAction(val)} action={true} del={true}/>
    </div>
    {index != 1 && val!='delete' ? <div className='action_gap'></div> : null}
  </>
    
}
