import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import BoxDesign from '../BoxDesign';

const TCommon = ({handleTrigger, selectedVal, method, isError, filter, name, icon}) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={`By ${name}`} desc={`Enter ${name}`} icon={icon} method={() => handleTrigger(filter, filter == 'handle' ? "selectedHandle" : filter == 'hashtag' ? 'selectedHashtag' : 'selectedKeyword','')}/>
      <div className="position-relative w-100" style={{ zIndex: 0 }} >
        <span className="h-34px w-34px text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-3 ps-1 f-center" style={{ zIndex: 9 }}>
          <FontAwesomeIcon icon={icon} iconClass="text-muted" />
        </span>
        <input type='text' className={`ps-7 fw-medium form-control form-control-sm ${isError ? 'is-invalid' : ''}`} value={selectedVal} onChange={event=>method(event)} />
        {isError && <div className="invalid-feedback d-block">{`Minimum ${filter == 'keyword' ? '3' :'2'} letters are required.`}</div>}
      </div>
    </div>
    <div className='action_gap'></div>
  </>

export default TCommon