import React, { Component } from 'react';
import { Container, Card, Form, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import './autopilot.scss'
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import ManageRules from './ManageRules';
import { DefaultRules, planUpgrade, RulesList, RuleStep } from './utils';
import HttpClient from '../../../../actions/http-client';
import { MODIFY_RULE, RULE_DELETE, RULE_LIST, RULE_SORT } from '../../../../actions/api';
import moment from 'moment';
import RuleListSkel from './RuleListSkel';
import { TOAST_LOADING, TOAST_UPDATE } from '../../../../utils';
import withRouter from '../../../routes/WithRouter';
import Swal from 'sweetalert2';


const reorder = (list, startIndex, endIndex) => {
   const result = Array.from(list);
   const [removed] = result.splice(startIndex, 1);
   result.splice(endIndex, 0, removed);
   return result;
};

const getItemStyle = (isDragging, draggableStyle) => ({
   userSelect: "none",
   borderBottom: `1px solid #F5F5F5`,
   ...draggableStyle
});



class AutoPilot extends Component {
   constructor(props) {
      super(props);
      this.state = {
         defRule:false,
         defIndex:0,
         ruleList:[],
         loading:false,
         ruleData:{},
         reset:false,
         wallId:localStorage.getItem('wallId'),
         spinner:false,
         editId:null,
         editStatus:null,
         editOrder:null,
         isDelete:false,
         isStatus:false
      };
      this.onDragEnd = this.onDragEnd.bind(this);
   }

   timeouts = []

   componentDidMount(){
      const { ruleList } = this.state
      if(ruleList.length<=0) this.getRules()
   }

   componentDidUpdate(){
      if(this.state.wallId != localStorage.getItem('wallId')){
         this.setState({reset:true, wallId:localStorage.getItem('wallId')}, ()=>
            { const timer1 = setTimeout(() => {
               this.setState({reset:false})
            }, 100);
            this.timeouts.push(timer1)
            this.getRules()}
         )
      }
   }

   getRules = (modify) => {
      if(!modify)this.setState({loading:true})
      new HttpClient().get(`${RULE_LIST}/${this.state.wallId}`).then(res=>{
         const { responseData } = res.data
         this.setState({ruleList:responseData, loading:false})
      })
   }

   modifyRule = (payload, status, callback) => {
      const {editId, wallId} = this.state
      const toastId = TOAST_LOADING()
      if(!status) this.setState({spinner:true})
      if(payload.actionsType == 3) this.setState({isStatus:true})
      new HttpClient().post(`${MODIFY_RULE}`,{...payload, wallId:wallId}).then(response=>{
         const { responseMessage } = response.data
         TOAST_UPDATE(toastId, "success", responseMessage)
         if(payload.actionsType == 1) this.setState({reset:true, spinner:false, isStatus:false}, ()=>{const timer2 = setTimeout(() => {
            this.setState({reset:false})
         }, 100);
         this.timeouts.push(timer2)})
         else if(payload.actionsType == 3 && payload.id == editId) {
            this.setState({editStatus:payload.status, spinner:false, isStatus:false}, ()=>{const timer3 = setTimeout(() => {
               this.setState({editStatus:null})
            }, 10);
            this.timeouts.push(timer3)})
         }
         else this.setState({spinner:false, isStatus:false},()=>callback ? callback():null)
         this.getRules(true)
      }).catch(err=>{
         this.setState({spinner: false})
         TOAST_UPDATE(toastId, "error", 'Some error occured, please try again later.')
      })
   }


   confirmDelete = (id) => {
       Swal.fire({
           title: `Are you sure?`,
           text: `Are you sure you want to delete this rule?`,
           icon: 'warning',
           showCancelButton: true,
           cancelButtonText: "Cancel",
           confirmButtonText: "Delete",
   
       }).then((result) => {
           if (result.value) {
               this.deleteRule(id)
           }
           else if (result.dismiss === "cancel") { }
       });
   }

   deleteRule = (id) => {
      let { ruleList, editId, wallId } = this.state
      const toastId = TOAST_LOADING()
      this.setState({isDelete:true})
      new HttpClient().delete(`${RULE_DELETE}/${id}/${wallId}`).then(response=>{
         const { responseMessage } = response.data
         TOAST_UPDATE(toastId, "success", responseMessage)
         if(id == editId) this.setState({reset:true}, ()=>{ const timer4 = setTimeout(() => {
            this.setState({reset:false})
         }, 100);
         this.timeouts.push(timer4)})
         this.setState({ruleList:ruleList.filter(rule=>rule.id != id), isDelete:false})
      }).catch(err=>{
         TOAST_UPDATE(toastId, "error", 'Some error occured, please try again later.') 
      })
   }

   changeStatus = (id,status) => {
      const payload = {id:id, status:Number(status), actionsType:3}
      this.modifyRule(payload, true)
   }

   onDragEnd(result) {
      if (!result.destination) return;

      const items = reorder( this.state.ruleList, result.source.index, result.destination.index );
      const toastId = TOAST_LOADING()
      let rules= []
      items.map((item, index) => (rules.push({ [item.id]: index})));
      new HttpClient().post(RULE_SORT, {rules:rules, wallId:this.state.wallId}).then(response=>{
         let order = rules.find(rule=> rule.hasOwnProperty(this.state.editId))
         if(order) this.setState({editOrder:order[this.state.editId]}, ()=> {const timer5 = setTimeout(() => {
            this.setState({editOrder:null})
         }, 10);
         this.timeouts.push(timer5)})
         this.getRules(true)
         const { responseMessage } = response.data
         TOAST_UPDATE(toastId, "success", responseMessage)
      }).catch(err=>{
         this.getRules(true)
         TOAST_UPDATE(toastId, "error", 'Some error occured, please try again later.')
      })
      this.setState({ruleList:items});
   }

   setDefault = (index) => {
      this.setState({defRule:true, defIndex:index},() => {const timer6 = setTimeout(() => {
         this.setState({defRule:false, defIndex:0})
      }, 200);
      this.timeouts.push(timer6)})
   }

   componentWillUnmount() {
      this.timeouts.forEach((timeout) => clearTimeout(timeout));
      this.timeouts = []; // Reset the array
   }

   render() {
      const { defRule, defIndex, ruleList, loading, ruleData, reset, spinner, editId, editStatus, editOrder, isDelete, isStatus } = this.state
      const {planId, navigate, ownerId} = this.props
      return <div className='d-flex flex-column flex-column-fluid w-100 flex-position-center full-content'>
         <Container fluid>
            <Card className="a_header_">
               <Card.Header>
                  <div className='f-between flex-wrap w-100'>
                     <div className='d-flex align-items-center flex-wrap my-2'>
                        <h1 className='text-dark font-weight-bold me-4 fs-6 mb-0'>
                           Auto Pilot
                        </h1>
                     </div>
                  </div>
               </Card.Header>
               <Card.Body className="d-flex flex-wrap flex-lg-nowrap align-items-start p-0 aside_body flex-lg-row-reverse align-items-stretch">
                  <div className="d-lg-flex flex-column flex-shrink-0 overflow-auto rule_guide w-100">
                     {loading ? <RuleListSkel/> : ruleList.length == 0 ? <React.Fragment key={0}><div className="w-100">
                        <h4 className="border-bottom p-4 fs-6 mb-0">Here's all that you can do:</h4>
                     </div>
                     <div className='px-5 mt-3'>
                        {RuleStep?.length ? RuleStep.map((Item, index) =>
                           <div key={index} className=" rule-box d-flex flex-column position-relative pb-2">
                              <h5 className="d-flex fs-8 mb-0">
                                 <span className='bg-body d-inline-block' style={{zIndex:'9'}}>
                                    <div className="f-center me-2 h-26px w-26px rounded-1 bg-light-primary me-2" style={{}}>
                                       <FontAwesomeIcon icon={Item.icon} iconClass={'text-primary'} />
                                    </div>
                                 </span>
                                 {Item.title}
                              </h5>
                              <p className="fs-8 ps-5 ms-3">{Item.text}</p>
                           </div>
                        ) : null}
                     </div></React.Fragment> :

                     <>
                        <div className="w-100">
                           <h4 className="border-bottom p-4 fs-6 mb-0">Manage Rules</h4>
                        </div>
                              
                        <div className="d-flex flex-row border-bottom py-3 px-4">
                           {RulesList?.length ? RulesList.map((Item, index) => (
                              <div key={index} className={`fw-semibold ${index === 3 ? 'text-end' : ''}`} style={{width:`${index%2?'15':'35'}%`}}>
                                 {Item.heading}
                              </div>
                           )) : null}
                        </div>
                              
                        <DragDropContext onDragEnd={this.onDragEnd}  >
                           <Droppable droppableId="droppable" isDropDisabled={ruleList.length < 2 || planId==57 ? true : false}>
                              {(provided, snapshot) => (
                                 <div {...provided.droppableProps} ref={provided.innerRef} >
                                    {ruleList.map((rule, index) => (
                                       <Draggable key={rule.id} draggableId={String(rule.id)} index={index}>
                                          {(provided, snapshot) => (
                                             <div ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps} style={getItemStyle(snapshot.isDragging, provided.draggableProps.style)} className='bg-body border-bottom'>                                                
                                                   <div className="d-flex align-items-center flex-row py-2 px-4 pe-1">
                                                      <div className='d-flex p-1' style={{width: '35%'}}>
                                                         <FontAwesomeIcon icon={'grip-dots-vertical'} iconClass="me-2 mt-1 text-muted" />   
                                                         <span className='d-inline-block text-break text-dark'>{rule.name}</span>
                                                      </div>
                                                      <div className='p-1' style={{width: '15%'}}>
                                                         <Form.Check type="switch" checked={rule.status} onChange={() => planId == 57 ? planUpgrade(navigate) : this.changeStatus(rule.id, !rule.status)} disabled={isStatus}/>
                                                      </div>
                                                      <div className='p-1' style={{width: '35%'}}>
                                                      {rule.lastExecuted ? moment.unix(rule.lastExecuted).format("DD MMM YYYY") : '--'}
                                                      </div>
                                                      <div className="d-flex align-items-center justify-content-end text-end" style={{width: '15%'}}>

                                                         {<OverlayTrigger placement="top" overlay={<Tooltip>{'Edit'}</Tooltip>}>
                                                            <button id={`edit_${index}`} className='border-0 btn btn-icon btn-sm bg-transparent' style={{ background: '#F5F5F5' }} onClick={()=>this.setState({ruleData:rule, editId:rule.id}, ()=> {const timer7 = setTimeout(() => {
                                                               this.setState({ruleData:{}})
                                                            }, 100); this.timeouts.push(timer7)})}>
                                                               <FontAwesomeIcon icon={'pen-to-square'} iconClass="me-1 text-muted"/>
                                                            </button>
                                                         </OverlayTrigger>}

                                                         {<OverlayTrigger placement="top" overlay={<Tooltip>{'Delete'}</Tooltip>}>
                                                            <button id={`delete_${index}`} className='border-0 btn btn-icon btn-sm bg-transparent' aria-label="delete" style={{ background: '#F5F5F5' }} onClick={()=>planId == 57 ? planUpgrade(navigate) : this.confirmDelete(rule.id)} disabled={isDelete}>
                                                               <FontAwesomeIcon icon={'trash-can'} iconClass="me-1 text-muted" />
                                                            </button> 
                                                         </OverlayTrigger>}

                                                      </div>
                                                   </div>
                                                </div>
                                          )}
                                       </Draggable>
                                    ))}
                                    {provided.placeholder}
                                 </div>
                              )}
                           </Droppable>
                        </DragDropContext>

                     </>}

                     <div className="w-100 mb-4 mt-5">
                        <h4 className="border-bottom p-4 fs-6 mb-0">Here's Default Rules List</h4>
                     </div>

                     <div className="px-5 rules-list">
                        {DefaultRules?.length ? DefaultRules.map((Item, index) => 
                           <div className="d-flex rounded-1 border border primary p-4 mb-3 cursor-pointer prefix_rule" onClick={()=>this.setDefault(index+1)}>
                              <div className="d-flex flex-column">
                                 <h5 className="d-flex fs-8 mb-1">
                                    <div className={`f-center me-2 h-26px w-26px rounded-1 bg-light-${Item.class}`}>
                                       <FontAwesomeIcon icon={Item.icon} iconClass={`text-${Item.class}`}/>
                                    </div>
                                    {Item.title}
                                 </h5>
                                 <p className="ps-5 ms-3 mb-0">{Item.text}</p>
                              </div>
                           </div>
                        ) : null}
                     </div>
                  </div>
                  <div className="d-flex flex-grow-1 position-relative">
                     <ManageRules defRule={defRule} defIndex={defIndex} modifyRule={this.modifyRule} ruleList={ruleList} ruleData={ruleData} reset={reset} spinner={spinner} editId={editId} editStatus={editStatus} editOrder={editOrder} navigate={navigate} planId={planId} ownerId={ownerId}/>
                  </div>
               </Card.Body>
            </Card>
         </Container>
      </div>
   }
}

export default AutoPilot