import React from 'react';
import { Dropdown, Button } from 'react-bootstrap';
import CreatableSelect from 'react-select/creatable';
import BoxDesign from './BoxDesign';
import { ruleActnFilters } from './utils';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import CommonActions from './CommonActions';

export default function AutoActions(props) {

    const {actionTabs, handleAction, selectedActionTags, onSelectTag, onTagRemove, tagsList, isTriggerSelected, selectedVisibility} = props
    const options = ruleActnFilters(handleAction)
    return <>
        {!(actionTabs && Object.keys(actionTabs).length > 0) ? <Dropdown className="w-100 mb-2 rule_actions">
            <Dropdown.Toggle variant="outline-secondary" id="rule-action" className="border border-gray text-dark border-1 w-100 p-4 arrow_disabled rounded-2" disabled={isTriggerSelected ? false : true}>
                <FontAwesomeIcon icon={'plus'} iconClass="text-muted" />
                <span className="fs-9 fw-medium">Add Action</span>
            </Dropdown.Toggle>
            <Dropdown.Menu className="w-100">
                {options.map((item,index)=>{
                    return selectedVisibility == 1 && index == 0 ? null 
                    : selectedVisibility == 2 && index == 1 ? null 
                    :   <Dropdown.Item className="block-center py-2 mb-1" onClick={item.fnc} key={index}>
                            <FontAwesomeIcon icon={item.icon} iconClass="text-muted fs-6" />
                            <span className="menu-title">{item.name}</span>
                        </Dropdown.Item>
                })}
            </Dropdown.Menu>
        </Dropdown> : null}
        {actionTabs && Object.keys(actionTabs).map((action, index) => {

            if (action == "public") {
                return <CommonActions name ={'Public'} val={action} index={index} handleAction={handleAction} icon={'eye'} key={index}/>
            }

            else if (action == "private") {
                return <CommonActions name ={'Private'} val={action} index={index} handleAction={handleAction} icon={'eye-slash'} key={index}/>
            }

            else if (action == "addTag") {
                return <>
                    <div className="border-dashed border-1 border-gray rounded-2 p-3">
                        <BoxDesign name={"Add Tags"} icon={"ri-price-tag-3-line"} method={()=>handleAction("addTag", "selectedActionTags", [])} action={true}/>
                        <CreatableSelect className="selectpicker selectpicker-sm" classNamePrefix="selectpicker" isClearable options={tagsList} onChange={event => onSelectTag(event, "action")} value={""} />
                        {selectedActionTags && selectedActionTags.length > 0 ?
                            <div className="d-flex w-100 flex-wrap w-100 mt-1">
                                {selectedActionTags.map((item, index) => {
                                    return <div className="badge bg-secondary rounded-pill text-gray-800 border me-1 mt-1 px-2 d-flex align-items-center" style={{ top: 0, maxWidth: '100%' }}>
                                        <span className="text-break" style={{ whiteSpace: 'normal' }}>{item}</span>
                                        <Button variant='link' className="btn p-0 ms-1 text-decoration-none" onClick={onTagRemove(index, "action")}>
                                            <FontAwesomeIcon icon={'xmark'} iconClass="text-muted me-1" />
                                        </Button>
                                    </div>
                                })}
                            </div> : null}
                    </div>
                {index != 1 ? <div className='action_gap'></div> : null} </>
            }

            else if (action == "delete") {
                return <CommonActions name ={'Delete'} val={action} index={index} handleAction={handleAction} icon={'trash-can'} key={index}/>
            }

            else return null
        })}

        {actionTabs && Object.keys(actionTabs).length > 0 ? !actionTabs.hasOwnProperty("delete") ? Object.keys(actionTabs).length < 2 ? <Dropdown className="w-100 text-center">
            <Dropdown.Toggle variant="outline-secondary" id="rule-dd" className="p-0 border-0 arrow_disabled">
                <FontAwesomeIcon type={'light'} icon={'circle-plus'} iconClass="text-muted me-0 fs-4" /> 
            </Dropdown.Toggle>
            <Dropdown.Menu className="w-100 more_actions">
                {options.map((item,index) => {
                    return index != 3 ?  

                    index == 0 || index == 1 ? !(actionTabs.hasOwnProperty("public") || actionTabs.hasOwnProperty("private")) ? 
                    selectedVisibility == 1 && index == 0 ? null 
                    : selectedVisibility == 2 && index == 1 ? null :
                    <Dropdown.Item className="block-center py-2 mb-1" onClick={item.fnc}>
                        <FontAwesomeIcon icon={item.icon} iconClass="text-muted fs-6" />
                        <span className="menu-title">{item.name}</span>
                    </Dropdown.Item> : null : 
                    
                    !(actionTabs.hasOwnProperty(item.propName)) ?
                    <Dropdown.Item className="block-center py-2 mb-1" onClick={item.fnc}>
                        <FontAwesomeIcon icon={item.icon} iconClass="text-muted fs-6" />
                        <span className="menu-title">{item.name}</span>
                    </Dropdown.Item>
                    :null
                : null})}
            </Dropdown.Menu>
        </Dropdown> : null : null : null}
    </>
}