import { Button } from 'react-bootstrap';
import CreatableSelect from 'react-select/creatable';
import BoxDesign from '../BoxDesign';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';

const TTag = ({handleTrigger, onSelectTag, onTagRemove, selectedTags, tagsList}) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={"Add Tags"} desc={"Select the preferred tag"} icon={"tags"} method={()=>handleTrigger("tags", "selectedTags", [])}/>
      <CreatableSelect placeholder="Search or Create tag..." className="selectpicker selectpicker-sm" classNamePrefix="selectpicker" isClearable options={tagsList} onChange={event=>onSelectTag(event,"filter")} value="" />
      {selectedTags && selectedTags.length > 0 ? <div className="d-flex w-100 flex-wrap w-100 mt-1">
        { selectedTags.map((item, index)=>{ return <div className="badge bg-secondary rounded-pill text-gray-800 border me-1 mt-1 px-2 d-flex align-items-center" style={{ top: 0, maxWidth: '100%' }}>
          <span className="text-break" style={{ whiteSpace: 'normal' }}>{item}</span>
          <Button variant='link' className="btn p-0 ms-1 text-decoration-none" onClick={onTagRemove(index,"filter")}>
            {/* <i className="ri-close-line me-0"></i> */}
            <FontAwesomeIcon icon={'xmark'} iconClass="text-muted" />
          </Button>
        </div>})}
      </div> : null}
    </div>
    <div className='action_gap'></div>
  </>
export default TTag
