import React from 'react';
import { Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';

export default function BoxDesign(props) {

    const {name, desc, icon, method, svgIcon, action, del} = props
    return <div className={`d-flex align-items-center rounded-0 ${del ? "" : "mb-2"}`}>
        <div className={`d-flex flex-grow-1 ${action ? 'align-items-center' : ''}`}>
        {!action ? 
            <div className="symbol symbol-36 bg-light-primary me-2">
                <FontAwesomeIcon icon={icon} iconClass="text-primary fs-6" />
            </div>
            : <FontAwesomeIcon icon={icon} iconClass="text-priamry me-1" />
        }
        <div>
            <p className={`fs-9 fw-bold mb-0 text-gray-800`}>{name}</p>
            {desc ? <span className="fs-9 text-gray-500">{desc}</span>: null}
        </div>
        </div>
        <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
        <Button variant='icon' className="btn-active-light p-0 w-30px h-30px transition-none border-0" aria-label="delete" onClick={method}>
            <FontAwesomeIcon icon={'trash-can'} iconClass="text-muted" />
        </Button>
        </OverlayTrigger>
    </div>
}