import { Dropdown } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { mediaAssets } from '../utils';

const TMedia = ({handleTrigger, selectedMedia, onSelectMediaType, selectAllMediaTypes}) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={"Media Type"} desc={"Choose the type of media"} icon={"image"} method={()=>handleTrigger("assetsType", 'selectedMedia', [] )}/>
      <Dropdown className="w-100" autoClose="outside">
        <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
          <div className="d-flex align-items-center flex-grow-1">
            {selectedMedia && selectedMedia.length== 1 ? <i className={`${selectedMedia.includes("1") ? "ri-text" : selectedMedia.includes("2,4") ? "ri-image-line" : "ri-movie-line" } text-gray-800 icon-6 fw-normal`}></i> : null}
            <p className="fs-9 fw-medium mb-0">{selectedMedia.length > 0 ? selectedMedia.length==1 ? selectedMedia.includes("1") ? "Text" : selectedMedia.includes("2,4") ? "Image" : "Video"  :`${selectedMedia.length} selected` :"Select Media Type"}</p>
          </div>
        </Dropdown.Toggle>
        <Dropdown.Menu className="w-100">
          <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
            <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-MediaType">
            Select All
            </label>
            <input className="form-check-input" type="checkbox" name="" id="tri-MediaType" onChange={event=>selectAllMediaTypes(event)} checked={selectedMedia.length==3} />
          </div>
          {mediaAssets.map((item,index)=> {
            return <div className='mb-1 dropdown-item' key={index}>
              <label className="form-check flex-grow-1 align-items-center" htmlFor={item.id}>
                <FontAwesomeIcon icon={item.icon} iconClass="text-muted fs-6" />
                <p className="fs-9 fw-medium mb-0">{item.name}</p>
              </label>
              <input className="form-check-input" type="checkbox" name="" value={item.icon} id={item.id} onChange={event=>onSelectMediaType(item.val)} checked={selectedMedia.includes(item.val)} />
            </div>
          })}
        </Dropdown.Menu>
      </Dropdown>
    </div>
    <div className='action_gap'></div>
  </>

export default TMedia