import React, { Component } from 'react'
import BoxDesign from '../BoxDesign'
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { Button, Form, OverlayTrigger, Tooltip } from 'react-bootstrap'
import UppyUpload from '../../../../../utils/uppyUpload'
import { FileInput } from "@uppy/react";
import { TEMP_URL } from '../../../../../actions/constants'

class TProfanity extends Component {
  uppyUploader = new UppyUpload(['.csv'])
  state={text:'Upload Your File', name:''}

  handleFilePickerClick = () => {
    const fileInput = document.querySelector('input[type="file"]')
    if (fileInput) {
      fileInput.click();
    }
  };

  componentDidMount() {
    const { setProfanity } = this.props
    this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
        this.setState({ loading: false, name: result.name }, () => {
            setProfanity(`${TEMP_URL}${result.id}.${result.ext}`)
        })
    })

    this.uppyUploader.uppy.on('upload-progress', (file, progress) => {
      if(this.state.text.includes('File')) this.setState({text:'Loading...'})
    });
  }

  componentWillUnmount(){
    this.uppyUploader.uppy.cancelAll()
  }



  render() {
    const {handleTrigger, onChangeProfanity, preProfanity, defaultProf, profName} = this.props
    const {text, name} = this.state
    return (<>
        <div className="border-dashed border-1 border-gray rounded-2 p-3">
            <BoxDesign name={"Profanity Filter"} desc={"Apply Profanity"} icon={"ban"} method={()=>handleTrigger("profanity", "profName", '')} svgIcon={true}/>
            <div className="d-flex justify-content-between mt-4">
                <div className="d-flex align-items-center w-75">
                    <Form.Check type="switch" checked={preProfanity} onChange={onChangeProfanity}/>
                    <p className="ms-2 mb-0 cursor-pointer text-ellipsis overflow-hidden w-75">{preProfanity ? 'Predefined CSV' : name ? name : profName ? profName.split('/').pop() : text}</p>
                </div>

                {!preProfanity && 
                <OverlayTrigger placement="top" overlay={<Tooltip>Upload CSV</Tooltip>}> 
                  <Button variant="outline-secondary" className='f-center border rounded-circle h-30px w-30px p-0' onClick={()=>!preProfanity ? this.handleFilePickerClick() : null}>
                      <FontAwesomeIcon icon={`arrow-up-to-line`} iconClass="me-0 mt-1 text-muted fs-6" />
                  </Button> 
                </OverlayTrigger>}

                <OverlayTrigger placement="top" overlay={<Tooltip>Download CSV</Tooltip>}> 
                  <Button variant="outline-secondary" className='f-center border rounded-circle h-30px w-30px p-0' onClick={()=>preProfanity ? window.open(defaultProf): profName ? window.open(profName) : null}>
                      <FontAwesomeIcon icon={`arrow-down-to-line`} iconClass="me-0 mt-1 text-muted fs-6" />
                  </Button> 
                </OverlayTrigger>
                
            </div>
        </div>
        <div></div>

        <div className="d-none">
          <FileInput uppy={this.uppyUploader.uppy} id="upload_auto_csv" />
        </div>
        <div className='action_gap'></div>
      </>
    )
  }
}

export default TProfanity
