import React, { Component } from 'react';
import { Dropdown, Image } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';

class TNetwork extends Component {
  state = {
    networkList: []
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ networkList: this.props.networkList })
    }, 100);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ networkList: nextProps.networkList })
  }

  onSearchNetwork = (event) => {
    const { networkList } = this.props
    const results = networkList.filter(network => network.name.toLowerCase().includes(event.target.value.toLowerCase()))
    this.setState({ networkList: results })
  }

  render() {
    const { handleTrigger, selectedNetworks, onSelectNetwork, selectAllNetworks } = this.props
    const { networkList } = this.state
    return <>
      <div className="border-dashed border-1 border-gray rounded-2 p-3">
        <BoxDesign name={"Network"} desc={"Choose the required network"} icon={"chart-network"} svgIcon={true} method={()=>handleTrigger("networks", "selectedNetworks", [])} />
        <Dropdown className="w-100" autoClose="outside">
          <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
            <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`filter_${GET_NETWORK_NAME(selectedNetworks[0])}`}>
              <span className="form-check-label text-gray-800 text-ellipsis fw-medium ps-0">{selectedNetworks.length > 0 ? selectedNetworks.length == 1 || (selectedNetworks.length == 2 && selectedNetworks.includes(18) && selectedNetworks.includes(2)) ? GET_NETWORK_NAME(selectedNetworks[0]) : `${selectedNetworks.includes(2) ? selectedNetworks.length - 1 : selectedNetworks.length} selected` : "Select Network"}</span>
            </label>
          </Dropdown.Toggle>
          <Dropdown.Menu className="w-100 overflow-auto" style={{ maxHeight: 400 }}>
            <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
              <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-searchNetwork">
                Select All
              </label>
              <input className="form-check-input" type="checkbox" id="tri-searchNetwork" onChange={event => selectAllNetworks(event)} checked={selectedNetworks.length == this.props.networkList.length + 1} />
            </div>
            <div className="form-group mb-2">
              <input type="text" className="form-control form-control-sm" placeholder="Search your network" onChange={event => this.onSearchNetwork(event)} />
            </div>
            {networkList && networkList.length > 0 ? networkList.map(network => <div className='dropdown-item' key={network.id}>
              <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`tri-network-${network.id}`}>
                {network.id == 29 ?
                  <FontAwesomeIcon type={'solid'} icon={'star'} iconClass={'text-warning fs-6'} />
                  :
                  <Image height={20} width={20} className='me-2' src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt={GET_NETWORK_NAME(network.id)} fluid />
                }
                <span className="form-check-label text-gray-800 text-ellipsis fw-medium">{network.name}</span>
              </label>
              <input className="form-check-input flex-shrink-0" type="checkbox" id={`tri-network-${network.id}`} checked={selectedNetworks.includes(network.id)} onChange={event => onSelectNetwork(network.id)} />
            </div>) :
              <div className="py-4">
                <h6 className="text-center fs-9 mb-0 text-gray-700">No Networks Available</h6>
              </div>}
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div className='action_gap'></div>
    </>
  }
}

export default TNetwork